exports.push([module.id, "\n.el-select .el-input[data-v-71d4d859] {\r\n  width: 100px;\n}\n.pagination_position[data-v-71d4d859] {\r\n  padding-top: 20px;\n}\n.echarts[data-v-71d4d859] {\r\n  height: 300px;\n}\r\n", "",{"version":3,"sources":["src/views/contents/loan/directRejectionRule.vue"],"names":[],"mappings":";AAwEA;EACA,YAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,aAAA;AACA","file":"directRejectionRule.vue?vue&type=style&index=0&id=71d4d859&scoped=true&lang=css&","sourcesContent":["<template>\r\n  <el-tabs v-model=\"activeName\" @tab-click=\"handleClick\">\r\n    <el-tab-pane label=\"直拒规则分布\" name=\"first\">\r\n      <div class=\"p_6\">\r\n        <div class=\"search-wrapper\">\r\n          <div class=\"search-content\">\r\n            <el-select\r\n              v-model=\"searchParam.device\"\r\n              slot=\"prepend\"\r\n              placeholder=\"手机设备\"\r\n              style=\"width: 100px;\"\r\n            >\r\n              <el-option\r\n                v-for=\"item in deviceOptions\"\r\n                :key=\"item.value\"\r\n                :label=\"item.name\"\r\n                :value=\"item.value\"\r\n              ></el-option>\r\n            </el-select>\r\n            <el-date-picker v-model=\"searchParam.dateRange\" type=\"daterange\" placeholder=\"选择日期范围\"></el-date-picker>\r\n          </div>\r\n          <div class=\"search-btn\">\r\n            <el-button slot=\"append\" icon=\"search\" @click=\"search\" style=\"width:80px; \">查询</el-button>\r\n          </div>\r\n        </div>\r\n        <el-table\r\n          :data=\"tableData\"\r\n          style=\"width: 100%\"\r\n          border\r\n          :default-sort=\"{prop: 'date', order: 'descending'}\"\r\n        >\r\n          <el-table-column type=\"index\" align=\"center\" width=\"50\"></el-table-column>\r\n          <el-table-column prop=\"date\" label=\"日期\" align=\"center\" min-width=\"120\" sortable></el-table-column>\r\n          <el-table-column\r\n            prop=\"identifier\"\r\n            label=\"编号\"\r\n            align=\"center\"\r\n            width=\"80\"\r\n            sortable\r\n            :filters=\"identifier\"\r\n            :filter-method=\"filterIdentifier\"\r\n          >\r\n            <!--<template slot-scope='scope'>-->\r\n            <!--<el-tag :type='primary' close-transition>{{scope.row.identifier}}</el-tag>-->\r\n            <!--</template>-->\r\n          </el-table-column>\r\n          <el-table-column prop=\"description\" label=\"描述\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n          <el-table-column prop=\"total\" label=\"总进件数\" align=\"center\" min-width=\"120\" sortable></el-table-column>\r\n          <el-table-column prop=\"hits\" label=\"命中数\" align=\"center\" width=\"80\" sortable></el-table-column>\r\n          <el-table-column prop=\"singleHit\" label=\"单一命中数\" align=\"center\" min-width=\"120\" sortable></el-table-column>\r\n          <el-table-column prop=\"hitRatio\" label=\"命中比例\" align=\"center\" min-width=\"120\" sortable></el-table-column>\r\n        </el-table>\r\n        <div class=\"pagination_position\">\r\n          <el-pagination\r\n            @size-change=\"handleSizeChange\"\r\n            @current-change=\"handleCurrentChange\"\r\n            :current-page=\"currentPage\"\r\n            :page-sizes=\"pageSizes\"\r\n            :page-size=\"pageSize\"\r\n            layout=\"total, sizes, prev, pager, next, jumper\"\r\n            :total=\"total\"\r\n          ></el-pagination>\r\n        </div>\r\n      </div>\r\n    </el-tab-pane>\r\n    <div v-if=\"showDetail\">\r\n      <el-tab-pane name=\"second\"></el-tab-pane>\r\n    </div>\r\n  </el-tabs>\r\n</template>\r\n\r\n<style scoped>\r\n.el-select .el-input {\r\n  width: 100px;\r\n}\r\n\r\n.pagination_position {\r\n  padding-top: 20px;\r\n}\r\n\r\n.echarts {\r\n  height: 300px;\r\n}\r\n</style>\r\n<style>\r\n.el-table .cell,\r\n.el-table th > div {\r\n  padding-left: 5px;\r\n  padding-right: 5px;\r\n  box-sizing: border-box;\r\n  text-overflow: ellipsis;\r\n}\r\n</style>\r\n<script>\r\nimport Common from \"../../../utils/common\";\r\nimport { Notification } from \"element-ui\";\r\nimport formatDate from \"../../../utils/formatDate.js\";\r\n\r\nexport default {\r\n  //    components:{\r\n  //    },\r\n  data() {\r\n    let date = new Date();\r\n    let prevMonth = new Date(date.setDate(date.getDate() - 30));\r\n    let now = new Date();\r\n\r\n    //echarts data\r\n\r\n    function randomData() {\r\n      return Math.round(Math.random() * 1000);\r\n    }\r\n\r\n    return {\r\n      identifierObj: {\r\n        //          Y000:true,\r\n        //          Y001:true,\r\n        //          Y002:true,\r\n        //          Y003:true,\r\n        //          Y004:true,\r\n        //          Y005:true,\r\n        //          Y006:true,\r\n        //          Y007:true,\r\n        //          Y008:true,\r\n        //          Y009:true,\r\n        //          Y010:true,\r\n        //          Y011:true,\r\n        //          Y012:true,\r\n      },\r\n      identifier: [\r\n        //      \t\t{ text: 'Y000', value: 'Y000' },\r\n        //          { text: 'Y001', value: 'Y001' },\r\n        //          { text: 'Y002', value: 'Y002' },\r\n        //          { text: 'Y003', value: 'Y003' },\r\n        //          { text: 'Y004', value: 'Y004' },\r\n        //          { text: 'Y005', value: 'Y005' },\r\n        //          { text: 'Y006', value: 'Y006' },\r\n        //          { text: 'Y007', value: 'Y007' },\r\n        //          { text: 'Y008', value: 'Y008' },\r\n        //          { text: 'Y009', value: 'Y009' },\r\n        //          { text: 'Y010', value: 'Y010' },\r\n        //          { text: 'Y011', value: 'Y011' },\r\n        //          { text: 'Y012', value: 'Y012' },\r\n      ],\r\n      detailData: {},\r\n      deviceOptions: [\r\n        {\r\n          value: \"ALL\",\r\n          name: \"ALL\",\r\n        },\r\n        {\r\n          value: \"iPhone\",\r\n          name: \"iOS\",\r\n        },\r\n        {\r\n          value: \"Android\",\r\n          name: \"Android\",\r\n        },\r\n      ],\r\n      searchParam: {\r\n        device: \"ALL\",\r\n        dateRange: [prevMonth, now],\r\n      },\r\n      activeName: \"first\",\r\n      showDetail: false,\r\n      name: \"detail\",\r\n      total: 0,\r\n      currentPage: 1,\r\n      pageSizes: [10, 20, 50, 100, 200],\r\n      pageSize: 50,\r\n      tableData: [],\r\n      select: \"\",\r\n    };\r\n  },\r\n  methods: {\r\n    filterIdentifier(value, row) {\r\n      //console.log('value', value);\r\n      return row.identifier === value;\r\n    },\r\n    getIdentifiers() {\r\n      Common.ajax({\r\n        url: \"/api/loan/directTypes\",\r\n        type: \"GET\",\r\n        contentType: \"application/json\",\r\n        data: {},\r\n        success: response => {\r\n          //console.log(response);\r\n          if (response.data && response.data.list) {\r\n            //              response.data.list.map( (obj) => {\r\n            //                obj.text = obj.name;\r\n            //                return obj;\r\n            //              });\r\n            //              this.identifier = response.data.list;\r\n            response.data.list.forEach(obj => {\r\n              var idrObj = obj.name;\r\n              //console.log(idrObj);\r\n              if (!this.identifierObj.hasOwnProperty(idrObj)) {\r\n                this.identifierObj[idrObj] = true;\r\n                this.identifier.push({\r\n                  text: idrObj,\r\n                  value: idrObj,\r\n                });\r\n              }\r\n            });\r\n          }\r\n          //console.log('identifier',this.identifier);\r\n        },\r\n        error: err => {\r\n          //            let error = err.message || err;\r\n          //            Notification.error({\r\n          //              title: '错误',\r\n          //              message: error\r\n          //            });\r\n        },\r\n      });\r\n    },\r\n    getDevices() {\r\n      Common.ajax({\r\n        url: \"/api/requestParam/device\",\r\n        type: \"GET\",\r\n        contentType: \"application/json\",\r\n        success: response => {\r\n          if (response.data && response.data.list) {\r\n            this.deviceOptions = response.data.list;\r\n          }\r\n        },\r\n        error: err => {\r\n          this.deviceOptions = [\r\n            {\r\n              value: \"ALL\",\r\n              name: \"ALL\",\r\n            },\r\n            {\r\n              value: \"iPhone\",\r\n              name: \"IOS\",\r\n            },\r\n            {\r\n              value: \"Android\",\r\n              name: \"Android\",\r\n            },\r\n          ];\r\n        },\r\n      });\r\n    },\r\n    search() {\r\n      let self = this;\r\n      let dateRange = self.searchParam.dateRange;\r\n      Common.ajax({\r\n        url: \"/api/loan/directRejectionRule\",\r\n        type: \"POST\",\r\n        contentType: \"application/json\",\r\n        data: {\r\n          device: self.searchParam.device,\r\n          pageIndex: self.currentPage,\r\n          pageSize: self.pageSize,\r\n          beginTime: formatDate(dateRange[0], \"yyyy-MM-dd\"),\r\n          endTime: formatDate(dateRange[1], \"yyyy-MM-dd\"),\r\n        },\r\n        success: response => {\r\n          //console.log(response);\r\n          if (response.data && response.data.list) {\r\n            let data = response.data;\r\n            self.tableData = data.list;\r\n            data.list.forEach(obj => {\r\n              var idrObj = obj.identifier;\r\n              if (!this.identifierObj.hasOwnProperty(idrObj)) {\r\n                this.identifierObj[idrObj] = true;\r\n                this.identifier.push({\r\n                  text: idrObj,\r\n                  value: idrObj,\r\n                });\r\n              }\r\n            });\r\n            self.total = data.pageUtil.pageCount;\r\n          }\r\n        },\r\n        error(err) {\r\n          let error = err.message || err;\r\n          Notification.error({\r\n            title: \"错误\",\r\n            message: error,\r\n          });\r\n        },\r\n      });\r\n    },\r\n    removeLabel(event) {\r\n      //console.log(event);\r\n      event.stopPropagation();\r\n      this.activeName = \"first\";\r\n      this.showDetail = false;\r\n    },\r\n    handleSizeChange(val) {\r\n      this.pageSize = val;\r\n      //console.log(`每页 ${val} 条`);\r\n      this.search();\r\n    },\r\n    handleCurrentChange(val) {\r\n      this.currentPage = val;\r\n      //console.log(`当前页: ${val}`);\r\n      this.search();\r\n    },\r\n    handleClick(tab, event) {\r\n      let index = tab.index;\r\n      if (index == 0) {\r\n        this.activeName = \"first\";\r\n      }\r\n    },\r\n  },\r\n  created() {\r\n    //console.log('created', this);\r\n  },\r\n  mounted() {\r\n    //console.log('mounted', this);\r\n    this.getIdentifiers();\r\n    this.getDevices();\r\n    this.search();\r\n  },\r\n};\r\n</script>\r\n\r\n"]}]);
